@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  html {
    scroll-behavior: smooth;
  }
  
  body {
    font-family: 'Poppins', sans-serif;
    background-color: #FFFFCC; /* cream as base bg */
    color: #003900; /* dark emerald green for text */
  }
}

@layer components {
  .btn-primary {
    @apply bg-emerald hover:bg-emerald-600 text-cream font-semibold py-3 px-6 rounded-lg transition-all duration-300 transform hover:scale-105 shadow-lg hover:shadow-xl;
  }
  
  .btn-secondary {
    @apply bg-cream hover:bg-cream-200 text-emerald font-semibold py-3 px-6 rounded-lg border-2 border-emerald transition-all duration-300 transform hover:scale-105;
  }
  
  .section-padding {
    @apply py-16 md:py-24;
  }
  
  .container-custom {
    @apply max-w-7xl mx-auto px-4 sm:px-6 lg:px-8;
  }
} 